/* eslint-disable no-unused-vars */
import FramesColor from '../frames-list/framescolor';

const mainCanvas = document.getElementById('main-canvas');
const ctx = mainCanvas.getContext('2d');

export default class Eraser {
  constructor() {
    this.eraserCanvas();
  }

  eraserCanvas() {
    let isMouseDown = false;

    // mainCanvas.width = 32;
    // mainCanvas.height = 32;
    // let positionX;
    // let positionY;

    mainCanvas.addEventListener('mousedown', () => {
      isMouseDown = true;
      // positionX = e.offsetX;
      // positionY = e.offsetY;
    });

    function eraserDraw(e) {
      const eraserTool = document.getElementById('eraserTool');
      if (!eraserTool.classList.contains('chosen')) {
        ctx.globalCompositeOperation = 'source-over';
        mainCanvas.removeEventListener('mousemove', eraserDraw);
      }

      const chosenLine = document.querySelector('.chosen-line');
      const line = window.getComputedStyle(chosenLine).width.slice(0, -2);

      if (isMouseDown) {
        const canvasWidth = mainCanvas.width;
        const pixelWidth = 640 / canvasWidth;
        const penX = Math.floor(e.offsetX / pixelWidth);
        const penY = Math.floor(e.offsetY / pixelWidth);
        // ctx.globalCompositeOperation = 'destination-out';

        // ctx.fillRect(penX, penY, line / 7, line / 7);
        ctx.clearRect(penX, penY, line / 7, line / 7);

        const canvas = document.querySelectorAll('.frame');
        for (let i = 0; i < canvas.length; i += 1) {
          if (canvas[i].classList.contains('selected-frame')) {
            const ctxFrame = canvas[i].getContext('2d');
            ctxFrame.clearRect(0, 0, canvas[i].width, canvas[i].height);
            const frames = new FramesColor(mainCanvas, canvas[i]);
          }
        }
      }
    }

    mainCanvas.addEventListener('mousemove', eraserDraw);

    document.body.addEventListener('mouseup', () => {
      isMouseDown = false;

      // const canvas = document.querySelectorAll('.frame');
      //   for (let i = 0; i < canvas.length; i += 1) {
      //     if (canvas[i].classList.contains('selected-frame')) {
      //       const ctxFrame = canvas[i].getContext('2d');
      //       // ctxFrame.clearRect(0, 0, canvas[i].width, canvas[i].height);
      //       const frames = new FramesColor(mainCanvas, canvas[i]);
      //     }
      //   }
    });
  }
}
