/* eslint-disable class-methods-use-this */
/* eslint-disable no-unused-vars */
import FramesColor from '../frames-list/framescolor';

const mainCanvas = document.getElementById('main-canvas');
const ctx = mainCanvas.getContext('2d');

let isMouseDown = false;
let penX;
let penY;
let mainCanvasImageURL;

export default class Circle {
  constructor() {
    this.circleCanvas();
  }

  circleCanvas() {
    let lineWidth;
    mainCanvas.addEventListener('mousedown', (e) => {
      isMouseDown = true;

      const chosenLine = document.querySelector('.chosen-line');
      const line = window.getComputedStyle(chosenLine).width.slice(0, -2);
      lineWidth = line / 7;

      const canvasWidth = mainCanvas.width;
      const pixelWidth = 640 / canvasWidth;
      penX = Math.floor(e.offsetX / pixelWidth);
      penY = Math.floor(e.offsetY / pixelWidth);

      //   const canvasWidth = mainCanvas.width;
      //   const pixelWidth = 640 / canvasWidth;

      //   if (lineWidth < 1 || (lineWidth > 2 && lineWidth < 3)) {
      //     penX = Math.floor(e.offsetX / pixelWidth) + 1 / 2;
      //     penY = Math.floor(e.offsetY / pixelWidth) + 1 / 2;
      //   } else {
      //     penX = Math.floor(e.offsetX / pixelWidth);
      //     penY = Math.floor(e.offsetY / pixelWidth);
      //   }

      mainCanvasImageURL = mainCanvas.toDataURL('image/png');
    });

    function circleDraw(e) {
      const circleTool = document.getElementById('circleTool');
      if (!circleTool.classList.contains('chosen')) {
        mainCanvas.removeEventListener('mousemove', circleDraw);
      }

      const primaryColorInput = document.getElementById('primary-color');
      const primaryColor = primaryColorInput.value;

      ctx.lineWidth = lineWidth;

      if (isMouseDown) {
        const canvasWidth = mainCanvas.width;
        const pixelWidth = 640 / canvasWidth;

        ctx.clearRect(0, 0, canvasWidth, canvasWidth);

        const img = new Image(640, 640);
        img.src = mainCanvasImageURL;

        ctx.drawImage(img, 0, 0, canvasWidth, canvasWidth);

        ctx.strokeStyle = primaryColor;

        // if (lineWidth < 1 || (lineWidth > 2 && lineWidth < 3)) {
        //   targetX = Math.floor(e.offsetX / pixelWidth) + 1 / 2;
        //   targetY = Math.floor(e.offsetY / pixelWidth) + 1 / 2;
        // } else {
        //   targetX = Math.floor(e.offsetX / pixelWidth);
        //   targetY = Math.floor(e.offsetY / pixelWidth);
        // }

        const targetX = Math.floor(e.offsetX / pixelWidth);
        const targetY = Math.floor(e.offsetY / pixelWidth);

        const x = penX + ((targetX - penX) / 2);
        const y = penY + ((targetY - penY / 2));

        // console.log(penX, penY, targetX, targetY);

        const a = Math.abs(targetX - penX) / 2;
        const b = Math.abs(targetY - penY / 2);
        // console.log(a, b);

        // function drawEllipse(ctx, x, y, a, b) {
        // Запоминаем положение системы координат (CК) и масштаб
        ctx.save();
        ctx.beginPath();

        // Переносим СК в центр будущего эллипса
        ctx.translate(x, y);

        /*
            * Масштабируем по х.
            * Теперь нарисованная окружность вытянется в a / b раз
            * и станет эллипсом
            */

        ctx.scale(a / b, 1);

        // Рисуем окружность, которая благодаря масштабированию станет эллипсом
        ctx.arc(0, 0, b, 0, Math.PI * 2, true);

        // Восстанавливаем СК и масштаб
        ctx.restore();

        ctx.closePath();
        ctx.stroke();
        // }
        // drawEllipse(ctx, x, y, a, b);
      }
    }

    mainCanvas.addEventListener('mousemove', circleDraw);

    document.body.addEventListener('mouseup', () => {
      isMouseDown = false;

      const canvas = document.querySelectorAll('.frame');
      for (let i = 0; i < canvas.length; i += 1) {
        if (canvas[i].classList.contains('selected-frame')) {
          const ctxFrame = canvas[i].getContext('2d');
          // ctxFrame.clearRect(0, 0, canvas[i].width, canvas[i].height);
          const frames = new FramesColor(mainCanvas, canvas[i]);
        }
      }
    });
  }
}
