/* eslint-disable no-shadow */
/* eslint-disable no-bitwise */
/* eslint-disable class-methods-use-this */
/* eslint-disable no-unused-vars */
import FramesColor from '../frames-list/framescolor';

const mainCanvas = document.getElementById('main-canvas');
const ctx = mainCanvas.getContext('2d');

let positionX;
let positionY;
let isMouseDown = false;
let mainCanvasImageURL;

export default class Stroke {
  constructor() {
    this.strokeCanvas();
  }

  static brezLine(positionX, positionY, targetX, targetY) {
    const chosenLine = document.querySelector('.chosen-line');
    const line = window.getComputedStyle(chosenLine).width.slice(0, -2);
    const lineWidth = line / 7;
    // x1 |= 0; y1 |= 0; x2 |= 0; y2 |= 0;
    let dx = targetX - positionX;
    let dy = targetY - positionY;
    const sx = (dx > 0) - (dx < 0);
    const sy = (dy > 0) - (dy < 0);
    dx *= sx; dy *= sy;

    const primaryColorInput = document.getElementById('primary-color');
    const primaryColor = primaryColorInput.value;

    ctx.fillStyle = primaryColor;
    ctx.fillRect(positionX, positionY, lineWidth, lineWidth);
    if (!(dx || dy)) return;
    let d = 0;
    let x = positionX;
    let y = positionY;
    let v;
    if (dy < dx) {
      for (v = 0 || (dy << 15) / dx * sy; x ^ targetX; x += sx, d &= 32767) {
        ctx.fillRect(x, y += (d += v) >> 15, lineWidth, lineWidth);
      }
    } else {
      for (v = 0 || (dx << 15) / dy * sx; y ^ targetY; y += sy, d &= 32767) {
        ctx.fillRect(x += (d += v) >> 15, y, lineWidth, lineWidth);
      }
    }
  }

  strokeCanvas() {
    mainCanvas.addEventListener('mousedown', (e) => {
      isMouseDown = true;
      const canvasWidth = mainCanvas.width;
      const pixelWidth = 640 / canvasWidth;
      positionX = Math.floor(e.offsetX / pixelWidth);
      positionY = Math.floor(e.offsetY / pixelWidth);

      mainCanvasImageURL = mainCanvas.toDataURL('image/png');
    });

    function drawStroke(e) {
      const strokeTool = document.getElementById('strokeTool');
      if (!strokeTool.classList.contains('chosen')) {
        mainCanvas.removeEventListener('mousemove', drawStroke);
      }
      if (isMouseDown) {
        const canvasWidth = mainCanvas.width;
        const pixelWidth = 640 / canvasWidth;

        ctx.clearRect(0, 0, mainCanvas.width, mainCanvas.height);
        const img = new Image(640, 640);
        img.src = mainCanvasImageURL;

        ctx.drawImage(img, 0, 0, canvasWidth, canvasWidth);

        const targetX = Math.floor(e.offsetX / pixelWidth);
        const targetY = Math.floor(e.offsetY / pixelWidth);

        Stroke.brezLine(positionX, positionY, targetX, targetY);

        const canvas = document.querySelectorAll('.frame');
        for (let i = 0; i < canvas.length; i += 1) {
          if (canvas[i].classList.contains('selected-frame')) {
            const ctxFrame = canvas[i].getContext('2d');
            ctxFrame.clearRect(0, 0, canvas[i].width, canvas[i].height);
            const frames = new FramesColor(mainCanvas, canvas[i]);
          }
        }
      }
    }

    mainCanvas.addEventListener('mousemove', drawStroke);

    document.body.addEventListener('mouseup', () => {
      isMouseDown = false;
      ctx.beginPath();
    });
  }
}
