/* eslint-disable class-methods-use-this */
const mainCanvas = document.getElementById('main-canvas');
const ctx = mainCanvas.getContext('2d');

export default class Pipette {
  constructor(e) {
    this.e = e;
    this.pipetteSelect();
  }

  pipetteSelect() {
    const primaryColorInput = document.getElementById('primary-color');
    const canvasWidth = mainCanvas.width;
    const pixelWidth = 640 / canvasWidth;
    const x = Math.floor(this.e.offsetX / pixelWidth);
    const y = Math.floor(this.e.offsetY / pixelWidth);

    const pixel = ctx.getImageData(x, y, 1, 1);
    const { data } = pixel;

    const b = '#'.concat((`0${parseInt(data[0], 10).toString(16)}`).slice(-2),
      (`0${parseInt(data[1], 10).toString(16)}`).slice(-2),
      (`0${parseInt(data[2], 10).toString(16)}`).slice(-2));

    primaryColorInput.value = b;
  }
}
