/* eslint-disable operator-assignment */
/* eslint-disable class-methods-use-this */
/* eslint-disable no-unused-vars */
import FramesColor from '../frames-list/framescolor';

const mainCanvas = document.getElementById('main-canvas');
const ctx = mainCanvas.getContext('2d');
let isMouseDown = false;

export default class Lighten {
  constructor() {
    this.lightenSelect();
  }

  lightenSelect() {
    mainCanvas.addEventListener('mousedown', () => {
      isMouseDown = true;
    });

    function lightenDraw(e) {
      const lightenTool = document.getElementById('lightenTool');
      if (!lightenTool.classList.contains('chosen')) {
        mainCanvas.removeEventListener('mousemove', lightenDraw);
      }

      if (isMouseDown) {
        const canvasWidth = mainCanvas.width;
        const pixelWidth = 640 / canvasWidth;
        const x = Math.floor(e.offsetX / pixelWidth);
        const y = Math.floor(e.offsetY / pixelWidth);

        const chosenLine = document.querySelector('.chosen-line');
        const line = window.getComputedStyle(chosenLine).width.slice(0, -2);

        const pixel = ctx.getImageData(x - 1, y - 1, line / 7 + 1, line / 7 + 1);
        const { data } = pixel;

        for (let i = 0; i < data.length; i += 4) {
          data[i] = data[i] + 8;
          data[i + 1] = data[i + 1] + 8;
          data[i + 2] = data[i + 2] + 8;
        }

        ctx.putImageData(pixel, x - 1, y - 1);

        const canvas = document.querySelectorAll('.frame');
        for (let i = 0; i < canvas.length; i += 1) {
          if (canvas[i].classList.contains('selected-frame')) {
            const frames = new FramesColor(mainCanvas, canvas[i]);
          }
        }
      }
    }

    mainCanvas.addEventListener('mousemove', lightenDraw);

    mainCanvas.addEventListener('mouseup', () => {
      isMouseDown = false;
    });
  }
}
