import pipettePic from './images/pipette.png';
import strokePic from './images/stroke.png';
import mirrorPic from './images/mirror.png';
import rectPic from './images/rect.png';
import circlePic from './images/circle.png';
import handPic from './images/hand.png';
import paintPxPic from './images/paint-px.png';
import lightenPic from './images/lighten.png';
import delightenPic from './images/delighten.png';
import reflectPic from './images/reflect.png';
import rotatePic from './images/rotate.png';
import bucketPic from './images/bucket.png';
import brushPic from './images/brush.png';

import Pen from '../components/pen/pen';
import Eraser from '../components/eraser/eraser';
import Stroke from '../components/stroke/stroke';
import Pipette from '../components/pipette/pipette';
import Mirror from '../components/mirror/mirror';
import Rect from '../components/rect/rect';
import Circle from '../components/circle/circle';
import Move from '../components/move/move';
import PaintPx from '../components/paintPx/paintPx';
import Lighten from '../components/lighten/lighten';
import Delighten from '../components/delighten/delighten';
import Reflect from '../components/reflect/reflect';
import Rotate from '../components/rotate/rotate';
import Bucket from '../components/bucket/bucket';
import Brush from '../components/brush/brush';
import FramesAdd from '../components/frames-list/framesadd';
import FramesColor from '../components/frames-list/framescolor';

const toolWrapper = document.getElementById('tools');
const transformWrapper = document.getElementById('transform');
const mainCanvas = document.getElementById('main-canvas');

mainCanvas.width = 64;
mainCanvas.height = 64;

toolWrapper.innerHTML = `
<button class="pen tool" id="penTool" type="button"><img class="img-tool" id="pen" src=${penPic} alt="Pen"></button>
<button class="eraser tool" id="eraserTool" type="button"><img class="img-tool" id="eraser" src=${eraserPic} alt="Eraser"></button>
<button class="pipette tool" id="pipetteTool" type="button"><img class="img-tool" id="pipette" src=${pipettePic} alt="Pipette"></button>
<button class="stroke tool" id="strokeTool" type="button"><img class="img-tool" id="stroke" src=${strokePic} alt="Stroke"></button>
<button class="mirror tool" id="mirrorTool" type="button"><img class="img-tool" id="mirror" src=${mirrorPic} alt="Mirror"></button>
<button class="rect tool" id="rectTool" type="button"><img class="img-tool" id="rect" src=${rectPic} alt="Rectangle"></button>
<button class="circlee tool" id="circleTool" type="button"><img class="img-tool" id="circle" src=${circlePic} alt="Circle"></button>
<button class="movee tool" id="moveTool" type="button"><img class="img-tool" id="move" src=${handPic} alt="Move"></button>
<button class="lighten tool" id="lightenTool" type="button"><img class="img-tool" id="lighten" src=${lightenPic} alt="Lighten"></button>
<button class="delighten tool" id="delightenTool" type="button"><img class="img-tool" id="delighten" src=${delightenPic} alt="Delighten"></button>
<button class="paintPx tool" id="paintPxTool" type="button"><img class="img-tool" id="paintPx" src=${paintPxPic} alt="Paint px"></button>
<button class="bucket tool" id="bucketTool" type="button"><img class="img-tool" id="bucket" src=${bucketPic} alt="Bucket"></button>
<button class="brush tool" id="brushTool" type="button"><img class="img-tool" id="brush" src=${brushPic} alt="Brush"></button>`;

transformWrapper.innerHTML = `
<button class="reflect tool" id="reflectTool" type="button"><img class="img-tool" id="reflect" src=${reflectPic} alt="Reflect"></button>
<button class="rotate tool" id="rotateTool" type="button"><img class="img-tool" id="rotate" src=${rotatePic} alt="Rotate"></button>`;

export default class ToolsSelect {
  constructor() {
    this.addFirstFrame();
    this.toolSelect();
    this.addOtherFrames();
  }

  addFirstFrame() {
    document.addEventListener('DOMContentLoaded', () => {
      document.getElementById('remove').src = wasteBin;
      document.getElementById('copy').src = copyImg;
      const selectedFrame = document.getElementById('frame');
      selectedFrame.classList.add('selected-frame');
      ToolsSelect.lineWidth();
      const reflect = new Reflect();
      const rotate = new Rotate();
    });
  }

  addOtherFrames() {
    const newFrameBtn = document.getElementById('new-frame-button');
    newFrameBtn.addEventListener('click', () => {
      const frames = new FramesAdd(mainCanvas);
    }, { once: true });
  }

  static lineWidth() {
    const allLineWidth = document.querySelector('.line-width-wrapper');
    const lineWidths = document.querySelectorAll('.line-width');

    allLineWidth.addEventListener('click', () => {
      for (let i = 0; i < lineWidths.length; i += 1) {
        if (window.event.target === allLineWidth) {
          return;
        }
        if (lineWidths[i].classList.contains('chosen-line') && window.event.target === lineWidths[i]) {
          return;
        }
        if (lineWidths[i].classList.contains('chosen-line')) {
          lineWidths[i].classList.remove('chosen-line');
          // document.getElementsByTagName('body')[0].style.cursor = 'default';
        } else if (window.event.target === lineWidths[i]) {
          lineWidths[i].classList.add('chosen-line');
        }
      }
    });
  }

  toolSelect() {
    this.showCoorditanes();
    const tools = document.getElementById('tools');
    const tool = document.querySelectorAll('.tool');
    const toolImg = document.querySelectorAll('.img-tool');

    tools.addEventListener('click', () => {
      for (let i = 0; i < toolImg.length; i += 1) {
        if (tool[i].classList.contains('chosen')) {
          tool[i].classList.remove('chosen');
          // document.getElementsByTagName('body')[0].style.cursor = 'default';
        } else if (window.event.target === toolImg[i]) {
          tool[i].classList.add('chosen');
        }
      }
    });

    this.toolSelectCanvas();
    this.toolSelectEraser();
    this.toolSelectStroke();
    this.toolSelectPipette();
    this.toolSelectMirror();
    this.toolSelectRect();
    this.toolSelectCircle();
    this.toolSelectMove();
    this.toolSelectPaintPx();
    this.toolSelectLighten();
    this.toolSelectDelighten();
    this.toolSelectBucket();
    this.toolSelectBrush();
  }

  showCoorditanes() {
    mainCanvas.addEventListener('mousemove', (e) => {
      const canvasWidth = mainCanvas.width;
      const pixelWidth = 640 / canvasWidth;
      const penX = Math.floor(e.offsetX / pixelWidth);
      const penY = Math.floor(e.offsetY / pixelWidth);

      const coord = document.getElementById('coord');
      coord.innerHTML = ` ${penX}:${penY}`;
    });

    mainCanvas.addEventListener('mouseleave', (e) => {
      const coord = document.getElementById('coord');
      coord.innerHTML = ' ';
    });
  }

  toolSelectCanvas() {
    const penTool = document.getElementById('penTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (penTool.classList.contains('chosen')) {
        const pen = new Pen();
      }
    });
  }

  toolSelectEraser() {
    const eraserTool = document.getElementById('eraserTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (eraserTool.classList.contains('chosen')) {
        const eraser = new Eraser();
      }
    });
  }

  toolSelectStroke() {
    const strokeTool = document.getElementById('strokeTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (strokeTool.classList.contains('chosen')) {
        const stroke = new Stroke();
      }
    });
  }

  toolSelectPipette() {
    const pipetteTool = document.getElementById('pipetteTool');
    mainCanvas.addEventListener('click', (e) => {
      if (pipetteTool.classList.contains('chosen')) {
        const pipette = new Pipette(e);
      }
    });
  }

  toolSelectMirror() {
    const mirrorTool = document.getElementById('mirrorTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (mirrorTool.classList.contains('chosen')) {
        const mirror = new Mirror();
      }
    });
  }

  toolSelectRect() {
    const rectTool = document.getElementById('rectTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (rectTool.classList.contains('chosen')) {
        const rect = new Rect();
      }
    });
  }

  toolSelectCircle() {
    const circleTool = document.getElementById('circleTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (circleTool.classList.contains('chosen')) {
        const circle = new Circle();
      }
    });
  }

  toolSelectMove() {
    const moveTool = document.getElementById('moveTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (moveTool.classList.contains('chosen')) {
        const move = new Move();
      }
    });
  }

  toolSelectPaintPx() {
    const paintPxTool = document.getElementById('paintPxTool');
    mainCanvas.addEventListener('click', (e) => {
      if (paintPxTool.classList.contains('chosen')) {
        const paintPx = new PaintPx(e);
      }
    });
  }

  toolSelectLighten() {
    const lightenTool = document.getElementById('lightenTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (lightenTool.classList.contains('chosen')) {
        const lighten = new Lighten();
      }
    });
  }

  toolSelectDelighten() {
    const delightenTool = document.getElementById('delightenTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (delightenTool.classList.contains('chosen')) {
        const delighten = new Delighten();
      }
    });
  }

  toolSelectBucket() {
    const bucketTool = document.getElementById('bucketTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (bucketTool.classList.contains('chosen')) {
        const bucket = new Bucket();
      }
    });
  }

  toolSelectBrush() {
    const brushTool = document.getElementById('brushTool');
    mainCanvas.addEventListener('mouseenter', () => {
      if (brushTool.classList.contains('chosen')) {
        const brush = new Brush();
      }
    });
  }
}
